/*
 * Datepicker element
 */
.datepicker.dropdown-menu {
  width: 250px;
  height: 270px;
}
.datepicker.dropdown-menu button,
.datepicker.dropdown-menu span.btn {
  outline: none;
  border: 0px;
}
.datepicker.dropdown-menu thead abbr {
  border: none;
}
.datepicker.dropdown-menu tbody {
  height: 180px;
}
.datepicker.dropdown-menu tbody button,
.datepicker.dropdown-menu tbody span.btn {
  padding: 6px;
}
.datepicker.dropdown-menu.datepicker-mode-1 tbody button,
.datepicker.dropdown-menu.datepicker-mode-2 tbody button,
.datepicker.dropdown-menu.datepicker-mode-1 tbody span.btn,
.datepicker.dropdown-menu.datepicker-mode-2 tbody span.btn {
  height: 65px;
}
